"use client";

import Editor from "react-simple-code-editor";
import { highlight, languages } from "prismjs/components/prism-core";
import "prismjs/components/prism-clike";
import "prismjs/components/prism-javascript";
import "prismjs/themes/prism.css"; //Example style, you can use another

import {
    ResizableHandle,
    ResizablePanel,
    ResizablePanelGroup,
} from "@/components/ui/resizable";
import {
    Card,
    CardContent,
    CardDescription,
    CardFooter,
    CardHeader,
    CardTitle,
} from "@/components/ui/card";
import React from "react";
import { Button } from "@/components/ui/button";
import { runCode } from "./runner";

export default function Home() {
    const [code, setCode] = React.useState(
        `function twoSum(nums, target) {\n  // Write your code here\n}`,
    );
    const [output, setOutput] = React.useState<any>(null);

    const testCases = [
        { input: { nums: [2, 7, 11, 15], target: 9 }, output: [0, 1] },
        { input: { nums: [3, 2, 4], target: 6 }, output: [1, 2] },
        { input: { nums: [3, 3], target: 6 }, output: [0, 1] },
    ];

    const handleRunCode = async () => {
        const result = await runCode(code, testCases);
        setOutput(result);
    };

    return (
        <>
            <head>
                <title>Code Interview</title>
            </head>
            <ResizablePanelGroup
                direction="horizontal"
                className="min-h-screen min-w-screen"
            >
                <ResizablePanel defaultSize={50}>
                    <div className="flex h-full items-center justify-center p-6">
                        <Card className="h-full w-full">
                            <CardHeader>
                                <CardTitle>Two Sum</CardTitle>
                                <CardDescription>Easy</CardDescription>
                            </CardHeader>
                            <CardContent>
                                <p>
                                    Given an array of integers `nums` and an
                                    integer `target`, return *indices of the two
                                    numbers such that they add up to `target`*.
                                </p>
                                <p>
                                    You may assume that each input would have
                                    **exactly one solution**, and you may not
                                    use the same element twice.
                                </p>
                                <p>You can return the answer in any order.</p>
                                <p></p>
                                <p></p>
                                <p>**Example 1:**</p>
                                <p></p>
                                <p>Input: nums = [2,7,11,15], target = 9</p>
                                <p>Output: [0,1]</p>
                                <p>
                                    Explanation: Because nums[0] + nums[1] == 9,
                                    we return [0, 1].
                                </p>
                            </CardContent>
                        </Card>
                    </div>
                </ResizablePanel>
                <ResizableHandle withHandle />
                <ResizablePanel defaultSize={50}>
                    <div className="flex h-full flex-col p-6">
                        <Card className="h-full w-full">
                            <CardHeader>
                                <CardTitle>Code Editor</CardTitle>
                            </CardHeader>
                            <CardContent className="h-3/4">
                                <Editor
                                    value={code}
                                    onValueChange={(code) => setCode(code)}
                                    highlight={(code) =>
                                        highlight(code, languages.js)
                                    }
                                    padding={10}
                                    style={{
                                        fontFamily:
                                            '"Fira code", "Fira Mono", monospace',
                                        fontSize: 12,
                                        border: "1px solid #ddd",
                                        borderRadius: "4px",
                                        height: "100%",
                                    }}
                                />
                            </CardContent>
                            <CardFooter>
                                <Button onClick={handleRunCode}>
                                    Run Code
                                </Button>
                            </CardFooter>
                        </Card>
                        {output && (
                            <Card className="h-1/4 w-full mt-4">
                                <CardHeader>
                                    <CardTitle>Output</CardTitle>
                                </CardHeader>
                                <CardContent>
                                    {output.error && (
                                        <p className="text-red-500">
                                            {output.error}
                                        </p>
                                    )}
                                    {output.results &&
                                        output.results.map(
                                            (result: any, index: number) => (
                                                <div key={index}>
                                                    <p>
                                                        Test Case {index + 1}:{" "}
                                                        {result.success
                                                            ? "✅"
                                                            : "❌"}
                                                    </p>
                                                    <p>
                                                        Input:{" "}
                                                        {JSON.stringify(
                                                            result.input,
                                                        )}
                                                    </p>
                                                    <p>
                                                        Expected:{" "}
                                                        {JSON.stringify(
                                                            result.expected,
                                                        )}
                                                    </p>
                                                    <p>
                                                        Actual:{" "}
                                                        {JSON.stringify(
                                                            result.actual,
                                                        )}
                                                    </p>
                                                </div>
                                            ),
                                        )}
                                </CardContent>
                            </Card>
                        )}
                    </div>
                </ResizablePanel>
            </ResizablePanelGroup>
        </>
    );
}
